.section-4 {
    position: relative;
    padding-top: 20.625rem;
    padding-bottom: 3.9375rem;
    .container-img {
        position: absolute;
        width: 50%;
        height: 17rem;
        object-fit: cover;
        top: 0;
        right: 0;
    }
    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        background: $blue;
        height: 100%;
        width: 50%;
        z-index: -1;
    }
    &:after {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        background: $orange;
        height: 100%;
        width: 50%;
        z-index: -1;
    }
    .section-4__block {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h3 {
            font-size: 1.75rem;
            font-weight: 900;
            line-height: 150%;
        }
        p {
            font-size: 1rem;
            font-weight: 400;
            margin-top: 1rem;
            line-height: 150%;
        }
        .section-4__btn {
            font-size: 1.125rem;
            width: 14.375rem;
            line-height: 3.5rem;
            background: #fff;
            color: $blue;
            font-weight: 500;
            border-radius: 3px;
            margin-top: 2.5rem;
            transition: all 0.3s ease;
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            bottom: 0;
            &:hover {
                filter: contrast(1.3) drop-shadow(2px 7px 6px rgba(0, 0, 0, .3));
                transform: translateY(-0.1rem);
            }
        }
        &.block-right {
            .section-4__btn {
                color: $orange;
            }
        }
        &.block-left {
            p {
                padding-right: 6rem;
            }
        }
    }
    @media (max-width: 1309px){
        .section-4__block h3 {
            font-size: 1.7rem;
        }
        .section-4__block.block-left p {
            padding-right: 0;
        }
    }
    @media (max-width: 991px){
        padding-top: 19rem;
        padding-bottom: 3.5rem;
        .block-right {
            padding-left: 2rem;
        }
    }
    @media (max-width: 767px){
        padding-top: 17rem;
        padding-bottom: 3.2rem;
        .block-left {
            padding-right: 1rem;
        }
        .block-right {
            padding-left: 1rem;
        }
        .section-4__block .section-4__btn {
            width: 13rem;
            font-size: 1.1rem;
            line-height: 3.3rem;
            margin-top: 2.3rem;
        }
        .container-img {
            height: 15rem;
        }
    }
    @media (max-width: 575px){
        padding-top: 15rem;
        padding-bottom: 2.5rem;
        .container-img {
            height: 13rem;
        }
        .block-left {
            padding-right: 0rem;
        }
        .block-right {
            padding-left: 0rem;
        }
        .section-4__block-content {
            text-align: center;
        }
        .section-4__block p {
            text-align: justify;
        }
        .section-4__block h3 {
            font-size: 1.6rem;
        }
        .section-4__block .section-4__btn {
            width: 11rem;
            font-size: 1rem;
            line-height: 3rem;
            margin: 2rem auto 0 auto;
        }
    }
    @media (max-width: 460px){
        padding-top: 11.5rem;
        padding-bottom: 2.3rem;
        .container-img {
            height: 10rem;
        }

        .section-4__block p {
            margin-top: 0.7rem;
        }
        .section-4__block h3 {
            font-size: 1.4rem;
        }
        .section-4__block .section-4__btn {
            width: 10rem;
            font-size: 0.9rem;
            line-height: 2.7rem;
            margin: 1.5rem auto 0 auto;
        }
    }
}