.section-2 {
    background: $blue;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    padding: 12.9rem 0;
    overflow: hidden;
    &:before {
        content: '';
        display: block;
        height: 1px;
        position: absolute;
        top:0;
        width: 100%;
        background: linear-gradient(90deg, rgb(43, 167, 58) 0%, rgb(101, 201, 34) 100%);
        opacity: 0.4;
    }
    
    h1 {
        text-align: center;
        font-size: 3rem;
        line-height: 116.67%;
        font-weight: 900;
        
    }
    .arrow_down {
        position: absolute;
        bottom: 0;
        background: #fff;
        border-radius: 50%;
        width: 2.75rem;
        height: 2.75rem;
        color: $blue;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;
        cursor: pointer;
        &:after {
            content: '';
            display: block;
            position: absolute;
            width: 2.75rem;
            height: 1.2rem;
            background: #fff;
            bottom: 0;
            z-index: 0;
        }
    }
    @media (max-width: 576px){
        h1 {
            font-size: 2.5rem;
        }
    }
    @media (max-width: 430px){
        padding: 12rem 0;
        h1 {
            font-size: 2rem;
        }
    }
}

