.section-3 {
    padding: 3.5625rem 0;
    .block-over {
        color: $blue;
        h2 {
            font-size: 2rem;
            text-align: center;
            font-weight: 900;
        }
        p {
            font-size: 1.125rem;
            line-height: 155.56%;
            max-width: 800px;
            margin: 1.75rem auto 0 auto;
            font-weight: 500;
        }
    }
    @media (max-width: 430px){
        .block-over {
            h2 {
                font-size: 1.8rem;
            }
        }
    }
}