.section-5 {
    background: #DBDBDB;
    .gallery {
        max-width: 720px;
        margin: 0 auto;
        &__item {
            background: #fff;
            display: flex;
            padding: 1.25rem;
            &_img {
                margin-right: 1.25rem;
            }
            &_text {
                padding-right: 3.375rem;
                &-top {
                    color: $orange;
                    font-size: 1.125rem;
                    line-height: 133.33%;
                }
                &-bottom {
                    color: #434345;
                    font-size: 0.875rem;
                    line-height: 128.57%;
                    margin-top: 0.375rem;
                }
            }
        }
    }
    
}