.header {
    &_top {
        height: auto;
        color: $blue;
        padding-top: 1.75rem;
        .logo {
            float: left;
            width: 12.5rem;
        }
        .header_top-points{
            float: right;
            clear: right;
            display: flex;
            align-items: center;
            .number {
                width: 3.4375rem;
            }
            .stars {
                display: flex;
                align-items: center;
                flex-direction: column;
                justify-content: center;
                margin-left: 1rem;
                div {
                    font-size: 0.625rem;
                    line-height: 140%;
                }
                ul {
                    display: flex; 
                    li {
                        img {
                            width: 0.83125rem;
                        }
                    }
                }
            }
        }
        &-why {
            float: right;
            clear: right;
            
            font-size: 0.8125rem;
            display: flex;
            margin-top: 12.6px;
            div {
                font-weight: 500;
               
            }
            ul {
                display: flex;
                li {
                    margin-left: 1rem;
                    svg {
                        font-size: 0.6rem;
                        margin-right: 0.1rem;
                    }
                }
            }
        }
    }
    &__menu {
        background:$blue;
        margin-top: 1.25rem;
        nav {
            padding: 16px 0 20px 0;
        }
        ul {
            display: flex;
            li {
                margin-right: 2.75rem;
                position: relative;
                transition: all 0.5s ease;
                &:before {
                    content: '';
                    display: block;
                    background: rgba(255,255,255, 0);
                    transition: all 0.5s ease;
                }
                &:hover {
                    transform: scale(1.03) translateY(-0.1rem);
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        left: -10%;
                        top: -0.3rem;
                        height: 1.8rem;
                        width: 120%;
                        background: rgba(255,255,255, 0.1);
                        box-shadow: 2px 2px 3px rgba(0,0,0,0.2);
                    }
                }
                
                &:last-child {
                    margin-right: 0;
                }
                a {
                    color: #fff;
                    font-size: 0.875rem;
                    position: relative;
                    svg {
                        margin-right: 0.3125rem;
                    }
                    span {
                        font-size: 0.3rem;
                        position: absolute;
                        bottom: 3px; 
                        left: -5.5px;
                        font-weight: normal;
                    }
                }
            }
        }
    }
}
@media (max-width: 768px){
    .header_top {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        .logo {
            float: none;
            margin-right: 3rem;
            width: 10rem;
        }
        .header_top-points {
            float: none;
           
        }
        .header_top-why {
            float: none;
        }
    } 
    .header__menu ul li {
        margin-right: 2rem;
    }
}
@media (max-width: 576px){
    .header__menu ul {
        justify-content: space-between;
    }
    .header__menu ul li {

        margin-right: 0;
    }
    .header__menu ul li svg {
        display: none;
    }
    
}
@media (max-width: 430px){
    .header_top-why {
        flex-wrap: wrap;
        justify-content: center;
        flex-direction: column;
        display: flex;
        div {
            text-align: center;
        }
        ul {
            margin-top: 0.5rem;
            li {
                &:nth-child(1) {
                    margin-left: 0;
                }
            }
        }
        // div {
        //     width: 100%;
        // }
        // ul {
        //     width: 100%;
        // }
    }
}