
$blue: #002E41;
$orange: #D34F2E;

@import "reset";
@import "fonts";

html {
    font-size: 16px;
}
body {
    font-family: MetaPro;
    font-weight: 400;
    color: #fff;
}


@media (min-width: 1310px){
    .container {
        max-width: 1310px;
    }
}
@media (max-width: 1309px){
    html {
        font-size: 15px;
    }
}
@media (max-width: 1199px){
    html {
        font-size: 13px;
    }
}
@media (max-width: 768px){
    html {
        font-size: 12px;
    }
}
@media (max-width: 340px){
    html {
        font-size: 11px;
    }
}



